@charset "utf-8";

.login_form *{
  margin: 0;
  padding: 0;
}
.login_form{
  border: 0;
  text-align: center;
  margin: auto;
  width: 250px;
}
.login_form legend{
font-size: 20px; font-weight: bold;
}

.login_form input[type='text']{
  display: block;
  padding: 10px;
  margin: 10px auto;
  width: 250px;
}
.login_form label{
  display: none;
}
.login_form a{
  text-decoration: none;
  color: #949494;
  font-size: 13px;
}
.login_form a:first-of-type{
  margin-right: 5px;
}
.login_form a:last-of-type{
  margin-left: 5px;
}
.login_form p{
  text-align: right;
  color: #949494;
  margin: 10px 0;
}
.login_btn, .sign_btn{
width: 100%;
padding: 10px;
margin: 5px 0;
border-radius: 4px;
cursor: pointer;
}
.login_btn{
  color: #fff;
  background: #610977;
  border:1px solid #610977;
}
.sign_btn{
  color: #610977;
  background: #fff;
  border: 1px solid #610977;
}